.footcare-container {
   width: 90%;
   margin: 0;
   display: flex;
   flex-direction: column;
   align-items: center;

   a {
      color: #000000;
      text-decoration: none;
      font-weight: bold;
   }

   span {
      font-weight: bold;
   }

   .description {
      width: 70%;
      line-height: 2rem;
      font-size: 1.2rem;
      letter-spacing: 0.1rem;

      button {
         background-color: transparent;
         font-size: 1.2rem;
         letter-spacing: 0.1rem;
         border: none;
         border-bottom: 1px solid black;
         font-weight: bold;
         font-family: 'Poppins', sans-serif;
         cursor: pointer;
      }
   }
}

.footcare-modal-container {
   background-color: rgba($color: #000000, $alpha: 0.7);
   backdrop-filter: blur(5px);
   position: fixed;
   top: 0;
   left: 0;
   bottom: 0;
   right: 0;
   z-index: 1000;

   .footcare-modal {
      position: fixed;
      top: 50%;
      left: 50%;
      padding: 2rem;
      border-radius: 20px;
      transform: translate(-50%, -50%);
      background-color: secondaryColor(1);
      color: white;
      z-index: 1000;

      button {
         position: absolute;
         right: 2%;
         top: 5%;
         color: white;
         border: none;
         background-color: transparent;
         font-weight: bold;
         font-size: 1.2rem;
         cursor: pointer;
         transition: 0.5s;
      }

      button:hover {
         transform: rotate(360deg) scale(1.2);
      }
   }
}
